import {EventEmitter, Injectable} from '@angular/core';
import * as signalR from '@microsoft/signalr';
import {SignalViewModel} from 'src/app/core/models/customer/signal/signal-viewmodel';
import {CustomerUrlSignal} from 'src/app/core/utils/URL/customer-url/customer-signal/customer-url-signal';
import { LocalStorageService } from '../../../comon-services/local-storage-service/localstorage.service';

@Injectable({
	providedIn: 'root'
})
export class SignalService {
	private hubConnection: signalR.HubConnection;
	connectionId: string = 'abc';
	signalReceived = new EventEmitter<SignalViewModel>();
	signalNotificationCount = new EventEmitter<number>();
	constructor(private customerUrlSignal: CustomerUrlSignal, private localStorage:LocalStorageService) {
		this.buiConnection();
		this.startConnection();
	}
	public buiConnection = () => {
		this.hubConnection = new signalR.HubConnectionBuilder()
			.withUrl(this.customerUrlSignal.getConnection(), {
				accessTokenFactory: () => this.localStorage.getItem('customer-token')
			})
			.withAutomaticReconnect()
			.build();
	};

	public startConnection = () => {
		this.hubConnection
			.start()
			.then((data) => {
				console.log('connection starting ...: ' + data);
			})
			//.then(() => this.getConnectionId())
			.then(() => this.getNotificationCount())
			.catch((err) => {
				console.log('errroororooror: ' + err);
			});
	};

	public getNotificationCount = () => {
		this.hubConnection.on('SendNotificationCount', (data: number) => {
			console.log(data);
			this.signalNotificationCount.emit(data);
		});
	};
}
