import {SpinnerService} from './../../../../../../../core/services/comon-services/spinner/spinner.service';
import {Component, Input, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {ProductShowHomePageRespond} from 'src/app/core/models/product/respond/ProductShowHomePageRespond';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SortProductService} from 'src/app/core/services/comon-services/sort-product/sort-product.service';
import {environment} from 'src/environments/environment';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {SortProduct} from 'src/app/core/models/product/SortProduct';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';

@Component({
	selector: 'app-index-group-product',
	templateUrl: './index-group-product.component.html',
	styleUrls: ['./index-group-product.component.less'],
	providers: [DestroyService]
})
export class IndexGroupProductComponent implements OnInit {
	@Input() pageResult: PagedResult<ProductShowHomePageRespond>;
	productShowHomePageResponds: Array<ProductShowHomePageRespond> = []; // products will be displayed
	host: string = environment.apiUrl; //host of BE
	sortSelected: SortProduct = {}; //combo of sort
	constructor(
		private router: Router,
	) {}
	ngOnInit(): void {
		this.productShowHomePageResponds = this.pageResult.items;
	}
	pageChanged(page: number) {
		this.sortSelected.page = page; // receive the page number
		this.getProductByUrlType(); // navigated by sort and page
	}
	getProductByUrlType() {
		this.router.navigate([], {queryParams: this.sortSelected}); //navigate
	}
}
