import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {CartService} from './../../../../../core/services/customer/cart/cart.service';
import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {RequestBase} from 'src/app/core/models/common/request/RequestBase';
import {BookDetailRespond} from 'src/app/core/models/product/respond/BookDetailRespond';
import {environment} from 'src/environments/environment';
import {IndexService} from '../index.service';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {AuthService} from 'src/app/core/services/auth/auth.service';
import {takeUntil} from 'rxjs/operators';
import {SuggestService} from 'src/app/core/services/customer/suggest/suggest.service';
import {ProductShowHomePageRespond} from 'src/app/core/models/product/respond/ProductShowHomePageRespond';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {RatingService} from 'src/app/core/services/customer/rating/rating.service';
import {CommentProgressProductResponse} from 'src/app/core/models/product/respond/CommentProgressProductResponse';
import {ProductService} from 'src/app/core/services/customer/product/product.service';
import {GroupProductViewModel} from 'src/app/core/models/common/respond/ProductRespondBase';

@Component({
	selector: 'app-product-detail',
	templateUrl: './product-detail.component.html',
	styleUrls: ['./product-detail.component.less'],
	providers: [DestroyService]
})
export class ProductDetailComponent implements OnInit {
	isLoading = true; //skelton
	productId: number;
	languageCode = 'vn';
	productById: BookDetailRespond; //nếu không khai bao thì ban đầu sẽ undefine
	host: string = environment.apiUrl;
	addCartLoading: boolean;
	buyNowLoading: boolean;
	isRating: boolean = false;
	isFollowShopLoading = false;
	userRating: number = 0;
	isLogin = false;
	isPreOderProduct = false;
	isProductSuggestionProductSimilarLoading: boolean = false;
	isProductSuggestionProductSameShopLoading: boolean = false;
	isProductSuggestionProductCosinLoading: boolean = false;
	isProductCommentProgressLoading: boolean = false;
	isGroupProductsLoading = false;
	productCommentProgresses?: Array<CommentProgressProductResponse>;
	apiResultProductSuggestSameShop?: ApiResult<Array<ProductShowHomePageRespond>>;
	apiResultProductShowHomePageResponds?: ApiResult<Array<ProductShowHomePageRespond>>;
	apiResultProductSuggestCosin?: ApiResult<Array<ProductShowHomePageRespond>>;
	groupProducts: Array<GroupProductViewModel>;
	constructor(
		private indexService: IndexService,
		private route: ActivatedRoute,
		private cartService: CartService,
		private notificationService: NotificationService,
		private destroy$: DestroyService,
		private router: Router,
		private authService: AuthService,
		private suggestService: SuggestService,
		private appErrorHandler: AppErrorHandler,
		private ratingService: RatingService,
		private productService: ProductService
	) {
		this.addCartLoading = false;
		this.buyNowLoading = false;
		this.productId = 1;
		this.productById = {} as BookDetailRespond;
	}
	ngOnInit(): void {
		this.isLoading = true;
		if (this.route.snapshot.paramMap.get('productId')) {
			this.productId = +this.route.snapshot.paramMap.get('productId');
			if (this.productId) {
				const requestBase = {id: this.productId, languageCode: this.languageCode};
				this.checkLogin();
				this.getProductById(requestBase);
				this.getUserRating(this.productId);
				this.getProductSameShop(this.productId);
				this.getProductCosin(this.productId);
				this.getProductCommentProgresses();
				//this.getGroupProducts(this.productId)
			}
		}
	}
	checkLogin() {
		if (this.authService.isAuthenticated('customer-token')) {
			this.isLogin = true;
			this.getProductSimilar();
		}
	}
	getProductById(requestBase: RequestBase) {
		this.indexService
			.getProductById(requestBase)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result.isSuccessed) {
						if (result.resultObj) {
							console.log(result);
							this.productById = result.resultObj;
							let a = new Date();
							let endPreOrder = new Date(Date.parse(this.productById.dateEndPreOrderProduct.toString()));
							console.log(this.productById.dateEndPreOrderProduct);
							console.log(new Date(Date.parse(this.productById.dateEndPreOrderProduct.toString())));
							console.log(a.getMilliseconds());
							console.log('aloo' + this.isPreOderProduct);
							if (endPreOrder > a) {
								this.isPreOderProduct = true;
								console.log('aloo' + this.isPreOderProduct);
							}
							this.isLoading = false;
						}
					} else {
						if (result.message) {
							this.notificationService.showErrorNotification(result.message);
						}
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				},
				() => {}
			);
	}
	getUserRating(productId: number) {
		if (this.authService.isAuthenticated('customer-token')) {
			this.ratingService
				.getUserRating(productId)
				.pipe(takeUntil(this.destroy$))
				.subscribe((result) => {
					if (result) {
						if (result.resultObj) {
							this.userRating = result.resultObj;
							console.log(this.productById);
						} else {
							this.userRating = 0;
						}
					} else {
						this.userRating = 0;
					}
				});
		} else {
			this.userRating = 0;
		}
	}
	putChangeRating(point: number) {
		this.isRating = true;
		if (this.authService.isAuthenticated('customer-token')) {
			this.ratingService
				.putChangeRating({productId: this.productId, rating: point})
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(result) => {
						if (result) {
							if (result.isSuccessed) {
								if (result.message) {
									this.notificationService.showSuccessNotification(result.message);
								}
							} else {
								if (result.message) {
									this.notificationService.showErrorNotification(result.message);
								}
							}
						}
						this.isRating = false;
					},
					(error) => {
						this.isRating = false;
						this.appErrorHandler.handleErrorWithNoti(error);
					}
				);
		} else {
			this.router.navigate(['/login'], {queryParams: {returnUrl: this.router.routerState.snapshot.url}});
		}
	}
	addToCart(quantityNumber: number) {
		this.addCartLoading = true;
		if (this.authService.isAuthenticated('customer-token')) {
			this.cartService
				.addToCart(this.productId, quantityNumber, 1)
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(result) => {
						if (result.isSuccessed) {
							if (result.message) {
								this.notificationService.showSuccessNotification(result.message);
							}
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							}
						}
						this.addCartLoading = false;
					},
					(error) => {
						this.addCartLoading = false;
						this.appErrorHandler.handleErrorWithNoti(error);
					},
					() => {
						this.addCartLoading = false;
					}
				);
		} else {
			this.router.navigate(['/login'], {queryParams: {returnUrl: this.router.routerState.snapshot.url}});
		}
	}
	addGroupProductToCart(groupProductId: number, quantityNumber: number) {
		this.addCartLoading = true;
		if (this.authService.isAuthenticated('customer-token')) {
			this.cartService
				.addToCart(groupProductId, quantityNumber, 2)
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(result) => {
						if (result.isSuccessed) {
							if (result.message) {
								this.notificationService.showSuccessNotification(result.message);
							}
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							}
						}
						this.addCartLoading = false;
					},
					(error) => {
						this.addCartLoading = false;
						this.appErrorHandler.handleErrorWithNoti(error);
					},
					() => {
						this.addCartLoading = false;
					}
				);
		} else {
			this.router.navigate(['/login'], {queryParams: {returnUrl: this.router.routerState.snapshot.url}});
		}
	}
	buyNow() {
		this.buyNowLoading = true;
		console.log('buynow');
		if (this.authService.isAuthenticated('customer-token')) {
			this.cartService
				.buyNow(this.productId)
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(result) => {
						if (result.isSuccessed && result.message) {
							this.notificationService.showSuccessNotification(result.message);
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							}
						}
						this.buyNowLoading = false;
					},
					(error) => {
						this.buyNowLoading = false;
						this.appErrorHandler.handleErrorWithNoti(error);
					},
					() => {
						this.buyNowLoading = false;
					}
				);
		} else {
			this.router.navigate(['/login'], {queryParams: {returnUrl: this.router.routerState.snapshot.url}});
		}
	}
	/**suggest product */
	getProductSimilar() {
		this.isProductSuggestionProductSimilarLoading = true;
		this.suggestService
			.getProductsBySimilarProduct()
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						this.apiResultProductShowHomePageResponds = result;
						console.log(this.apiResultProductShowHomePageResponds);
						this.isProductSuggestionProductSimilarLoading = false;
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	getProductSameShop(productById: number) {
		this.isProductSuggestionProductSameShopLoading = true;
		this.suggestService
			.getProductsSuggestSameShop(productById)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						this.apiResultProductSuggestSameShop = result;
						console.log(this.apiResultProductSuggestSameShop);
						this.isProductSuggestionProductSameShopLoading = false;
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	getProductCommentProgresses() {
		this.isProductCommentProgressLoading = true;
		this.indexService
			.getProductCommentProgress(this.productId)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					console.log(result);
					if (result) {
						this.productCommentProgresses = result.resultObj;
						console.log(this.productCommentProgresses);
						this.isProductCommentProgressLoading = false;
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	getProductCosin(productId: number) {
		this.isProductSuggestionProductCosinLoading = true;
		this.suggestService
			.getProductsByCosin(productId)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						this.apiResultProductSuggestCosin = result;
						console.log(this.apiResultProductSuggestCosin);
						this.isProductSuggestionProductCosinLoading = false;
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	getGroupProducts() {
		this.isGroupProductsLoading = true;
		console.log(this.productId);
		this.productService.getGroupProducts(this.productId).subscribe((result) => {
			if (result) {
				if (result.isSuccessed) {
					this.groupProducts = result.resultObj;
					console.log(this.groupProducts);
					this.isGroupProductsLoading = false;
				}
			}
		});
	}
}
