import {Observable} from 'rxjs';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {CustomerUrlRating} from 'src/app/core/utils/URL/customer-url/customer-rating/customer-url-rating';
import { ChangeRating } from 'src/app/core/models/customer/rating/request/ChangeRating';

@Injectable({
	providedIn: 'root'
})
export class RatingService {
	constructor(private customerUrlRating: CustomerUrlRating, private http: HttpClient) {}

	getUserRating(productId: number): Observable<ApiResult<number>> {
		return this.http.get<ApiResult<number>>(this.customerUrlRating.getUserRating(productId));
	}
	putChangeRating(changeRating:ChangeRating): Observable<ApiResult<boolean>> {
		return this.http.put<ApiResult<boolean>>(this.customerUrlRating.putChangeRating(), changeRating);
	}
}
