import {createFeatureSelector, createSelector} from '@ngrx/store';
import {CustomerLoginState} from './customer-login.state';

export const customerSelectLoginState = createFeatureSelector<CustomerLoginState>('customerlogin');
export const customerLoginStateSelector = createSelector(
	customerSelectLoginState,
	(state: CustomerLoginState) => state
);
export const customerIsAuthenticatedLoginStateSelector = createSelector(
	customerSelectLoginState,
	(state: CustomerLoginState) => state.isAuthenticated
);
export const customerLoginMessageStateSelector = createSelector(
	customerSelectLoginState,
	(state: CustomerLoginState) => state.message
);
export const customerLoginStatusStateSelector = createSelector(
	customerSelectLoginState,
	(state: CustomerLoginState) => state.status
);
