import {Injectable} from '@angular/core';
import {CustomerUrlBase} from './../customer-url-base';
@Injectable({
	providedIn: 'root'
})
export class CustomerUrlProductSuggest extends CustomerUrlBase {
	getProductsBySimilarProduct(): string {
		return this.customerUrl + '/suggestion/similar-product';
	}
	getProductsByCosin(productId: number): string {
		return this.customerUrl + '/suggestion/similar-product-cosin/' + productId;
	}
	getProductsSuggestSameShop(productId: number): string {
		return this.customerUrl + '/suggestion/same-shop/' + productId;
	}
}
