import {HttpParams} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import {CustomerUrlBase} from './../customer-url-base';
@Injectable({
	providedIn: 'root'
})
export class CustomerUrlNotification extends CustomerUrlBase {
	constructor() {
		super();
	}
	customerGetNotification() {
		return this.customerUrl + '/chat/notification';
	}
	customerGetNotificationCount() {
		return this.customerUrl + '/notification/notification-count';
	}
	customerGetNotificationList(request: PagingRequestBase) {
		let params = new HttpParams()
			.set('pageindex', request.pageIndex == undefined ? '1' : request.pageIndex.toString())
			.set('pagesize', request.pageSize == undefined ? '10' : request.pageSize.toString());

		return this.customerUrl + '/notification?' + params.toString();
	}
	customerPostNotificationHasReadList(): string {
		return this.customerUrl + '/notification';
	}
}
