import {debounceTime, switchMap} from 'rxjs/operators';
import {ProductService} from './../../../../core/services/customer/product/product.service';
import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {NzDrawerPlacement} from 'ng-zorro-antd/drawer';
import {select, Store} from '@ngrx/store';
import {CustomerAppState} from 'src/app/core/store/customer/customer-app.state';
import {selectSettingsLanguage, selectTheme} from 'src/app/core/store/customer/setting/customer-settings.selectors';
import {Language, Theme} from 'src/app/core/store/customer/setting/customer-settings.state';
import {
	actionSettingsChangeLanguage,
	actionSettingsChangeTheme
} from 'src/app/core/store/customer/setting/customer-setting.actions';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {environment} from 'src/environments/environment';
import {customerLoginResetAction} from 'src/app/core/store/customer/login/customer-login.actions';
import {Router} from '@angular/router';
import {customerInforCustomerStateSelector} from 'src/app/core/store/customer/infor/customer-infor.selector';
import {OnSearchRequest} from 'src/app/core/models/product/request/OnSearchRequest';
import {ProductShowHomePageRespond} from 'src/app/core/models/product/respond/ProductShowHomePageRespond';
import {Subject} from 'rxjs';
import {CategoryService} from 'src/app/core/services/customer/category/category.service';
import {CategoryVariationWithAliasResponseVM} from 'src/app/core/models/seller/category/categoryVariationWithAliasResponse.ViewModel';
import {AuthService} from 'src/app/core/services/auth/auth.service';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {LocalStorageService} from 'src/app/core/services/comon-services/local-storage-service/localstorage.service';
@Component({
	selector: 'app-customer-header',
	templateUrl: './customer-header.component.html',
	styleUrls: ['./customer-header.component.scss', './customer-header.component.less'],
	providers: [DestroyService],
	encapsulation: ViewEncapsulation.None
})
export class CustomerHeaderComponent implements OnInit {
	visible = false;
	isLogin?: boolean;
	placement: NzDrawerPlacement = 'left';
	cartProductCount: number;
	themes = [
		{value: 'dark', label: 'Dark|Tối'},
		{value: 'default', label: 'light|sáng'}
	];
	languages = [
		{value: 'customer-en', label: 'EN'},
		{value: 'customer-vn', label: 'VN'}
	];
	themeDefault: Theme;
	languageDefault: Language;
	customerInfor: any;
	onSearch: OnSearchRequest;
	pageIndex: number = 1;
	productShowHomePageResponds: Array<ProductShowHomePageRespond> = [];
	categoryVariationSelectList: Array<CategoryVariationWithAliasResponseVM> = [];
	host: string = environment.apiUrl;
	searchKeyWord = new Subject<string>();
	constructor(
		private store: Store<CustomerAppState>,
		private destroy$: DestroyService,
		private productService: ProductService,
		private categoryService: CategoryService,
		private authService: AuthService,
		private appErrorHandler: AppErrorHandler,
		private localStore: LocalStorageService,
		private router: Router
	) {
		this.themeDefault = 'default';
		this.languageDefault = 'customer-vn';
		this.isLogin = false;
		this.cartProductCount = 0;
		this.onSearch = {} as OnSearchRequest;
	}

	ngOnInit(): void {
		//console.log('trong header init');
		this.initCategoryVariatonsList();
		this.pageIndex = 1;
		this.checkLogin();
		this.store
			.select(selectTheme)
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				this.themeDefault = result != null ? result : 'default';
			});
		this.store.select(selectSettingsLanguage).subscribe(
			(result) => {
				this.languageDefault = result != null ? result : 'customer-vn';
			},
			(error) => {
				this.appErrorHandler.handleErrorWithNoti(error);
			}
		);
	}
	initCategoryVariatonsList() {
		this.categoryService
			.getCategoryVariationList()
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					this.categoryVariationSelectList = result.resultObj;
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	checkLogin() {
		if (this.authService.isAuthenticated('customer-token')) {
			this.isLogin = true;
			this.setCustomerInfor();
		} else {
			//this.appCookieService.remove('customer-token');
			this.localStore.removeItem('customer-token');
			this.store.dispatch(customerLoginResetAction({message: 'logout'}));

			console.log('customerAuthGuard: false');
		}
	}
	cancel() {
		return;
	}
	confirm() {
		this.logout();
	}
	logout() {
		this.authService.logout('customer', this.router)
	}
	setCustomerInfor() {
		this.store.pipe(select(customerInforCustomerStateSelector)).subscribe(
			(result) => {
				if (result) {
					const customerInforCookie = result;
					if (customerInforCookie.image) {
						this.customerInfor = {
							image: environment.apiUrl + '/' + customerInforCookie.image.imagepath,
							name: customerInforCookie.firstName + ' ' + customerInforCookie.lastName
						};
					} else {
						this.customerInfor = {
							image: environment.apiUrl + '/' + 'assets/img/customer-avatar.png',
							name: customerInforCookie.firstName + ' ' + 'customer no name'
						};
					}
				}
			},
			(error) => {
				this.appErrorHandler.handleErrorWithNoti(error);
			}
		);
	}
	onThemeSelect(theme: Theme): void {
		this.store.dispatch(actionSettingsChangeTheme({theme}));
	}
	onLanguageSelect(language: Language) {
		this.store.dispatch(actionSettingsChangeLanguage({language}));
	}
	open(): void {
		this.visible = !this.visible;
	}
	close(): void {
		this.visible = false;
	}

	changeSelectOfType(event: string) {
		this.onSearch.type = event;
		console.log(this.onSearch);
	}
	inputValue(event: Event) {
		this.searchKeyWord.next((event.target as HTMLInputElement).value);
		this.searchKeyWord
			.pipe(
				debounceTime(500),
				switchMap((value) =>
					this.productService.getProductBySearch({
						keyWord: value,
						type: 'book',
						pageIndex: this.pageIndex,
						pageSize: 10
					})
				)
			)
			.subscribe(
				(result) => {
					if (result.isSuccessed) {
						if (result.resultObj) {
							this.productShowHomePageResponds = result.resultObj.items;
						} else {
						}
					} else {
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	searchValue(onSearch: OnSearchRequest) {
		onSearch.pageIndex = this.pageIndex;
		onSearch.pageSize = 10;
	}
}
