import {Injectable} from '@angular/core';
import {
	CanActivate,
	ActivatedRouteSnapshot,
	RouterStateSnapshot,
	UrlTree,
	CanActivateChild,
	CanLoad,
	Route,
	UrlSegment,
	Router
} from '@angular/router';
import {Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {AuthService} from '../../services/auth/auth.service';
import {CustomerAppState} from '../../store/customer/customer-app.state';
import {customerLoginResetAction} from '../../store/customer/login/customer-login.actions';

@Injectable({
	providedIn: 'root'
})
export class CustomerAuthGuard implements CanActivate, CanActivateChild, CanLoad {
	constructor(
		public router: Router,
		private authService: AuthService,
		private store: Store<CustomerAppState>,
	) {}
	canActivateChild(
		_childRoute: ActivatedRouteSnapshot,
		state: RouterStateSnapshot
	): boolean | UrlTree | Observable<boolean | UrlTree> | Promise<boolean | UrlTree> {
		if (this.authService.isAuthenticated('customer-token')) {
			return true;
		} else {
			this.store.dispatch(customerLoginResetAction({message: 'logout'}));
			this.router.navigate(['/login'], {queryParams: {returnUrl: state.url}});
			console.log('customerAuthGuard: false');
			return false;
		}
	}
	canLoad(
		_route: Route,
		_segments: UrlSegment[]
	): boolean | UrlTree | Observable<boolean | UrlTree> | Promise<boolean | UrlTree> {
		throw new Error('Method not implemented.');
	}
	canActivate(
		_route: ActivatedRouteSnapshot,
		state: RouterStateSnapshot
	): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
		if (this.authService.isAuthenticated('customer-token')) {
			console.log('customerAuthGuard: true');
			return true;
		} else {
			this.store.dispatch(customerLoginResetAction({message: 'logout'}));
			this.router.navigate(['/login'], {queryParams: {returnUrl: state.url}});
			console.log('customerAuthGuard: false');
			return false;
		}
	}
}
