import {CustomerInforState} from './customer-infor.state';
import {createFeatureSelector, createSelector} from '@ngrx/store';

export const customerSelectInforState = createFeatureSelector<CustomerInforState>('customerinfor');
export const customerInforStateSelector = createSelector(
	customerSelectInforState,
	(state: CustomerInforState) => state
);
export const customerInforCustomerStateSelector = createSelector(
	customerSelectInforState,
	(state: CustomerInforState) => state.customer
);
