import { Injectable } from '@angular/core';
import {RequestBase} from 'src/app/core/models/common/request/RequestBase';
import {CustomerUrlBase} from './../customer-url-base';
@Injectable({
	providedIn: 'root'
})
export class CustomerUrlCategory extends CustomerUrlBase {
	customerGetCategoryVariationByCatgoryIdAPI(requestBase: RequestBase, categoryId: number): string {
		return this.customerUrl + `/category/${categoryId}/categoryvariations/${requestBase.languageCode}`;
		/*******************END CUSTOMER****************************/
	}
}
