import { createFeatureSelector, createSelector} from '@ngrx/store';
import { CustomerAppState } from '../customer-app.state';
import { CustomerSettingsState } from './customer-settings.state';


export const selectSettingsState = createFeatureSelector<CustomerAppState, CustomerSettingsState>('customersettings');
export const selectSettings = createSelector(selectSettingsState, (state: CustomerSettingsState) => state);
export const selectTheme = createSelector(selectSettings, (settings) => settings.theme);
export const selectSettingsLanguage = createSelector(selectSettings, (state: CustomerSettingsState) => state.language);
