import {HttpClient} from '@angular/common/http';
import {TranslateService} from '@ngx-translate/core';
import {takeUntil} from 'rxjs/operators';
import {Component, OnInit} from '@angular/core';
import {FormGroup, Validators, FormBuilder} from '@angular/forms';
import {CustomerAccountService} from 'src/app/core/services/auth/customer/customer-account.service';
import {environment} from 'src/environments/environment';
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {CustomerAppState} from 'src/app/core/store/customer/customer-app.state';
import {select, Store} from '@ngrx/store';
import {Router} from '@angular/router';
import {customerIsAuthenticatedLoginStateSelector} from 'src/app/core/store/customer/login/customer-login.selector';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {formatDate} from '@angular/common';
import fr from '@angular/common/locales/fr';
interface Option {
	value: string | number;
	label: string;
	children?: Option[] | string;
}
interface AreaProvinceArray {
	Id: string;
	Name: string;
	Districts: AreaDistrictArray[];
}
interface AreaDistrictArray {
	Id: string;
	Name: string;
	Wards: AreaWardArray[];
}
interface AreaWardArray {
	Id: string;
	Name: string;
	Level: string;
}
@Component({
	selector: 'app-customer-register',
	templateUrl: './customer-register.component.html',
	styleUrls: ['./customer-register.component.sass']
})
export class CustomerRegisterComponent implements OnInit {
	submitted = false;
	returnUrl = ''; //string to return url
	customerRegisterForm: FormGroup; //form group
	host: string = environment.apiUrl; //host
	clientHost: string = environment.clientUrl;
	ready = false; //ready to display image
	isHaveUploadAvatar: boolean;
	thumbshandlePreview: Array<{uid: string; url: string}> = []; //thumb iamge
	fileList: Array<File> = [];
	areaArray: Option[];
	valuesArea: any[] | null = null;
	today: number = Date.now();
	constructor(
		private formBuilder: FormBuilder,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler,
		private accountService: CustomerAccountService,
		private store: Store<CustomerAppState>,
		private router: Router,
		private translateService: TranslateService,
		private notificationService: NotificationService,
		private http: HttpClient
	) {
		this.isHaveUploadAvatar = false;
		this.customerRegisterForm = this.formBuilder.group({
			email: ['', [Validators.required, Validators.minLength(3), Validators.pattern]],
			passWord: ['', [Validators.required, Validators.minLength(8)]],
			passWordConfirm: ['', [Validators.required, Validators.minLength(8)]],
			phoneNumber: ['', [Validators.required, Validators.minLength(8)]],
			name: ['', [Validators.required, Validators.minLength(2)]],
			dob: [formatDate(this.today, 'DD-MM-YYYY', 'en'), [Validators.required, Validators.minLength(3)]],
			// country: ['', [Validators.required, Validators.minLength(3)]],
			province: ['', [Validators.required, Validators.minLength(3)]],
			// city: ['', [Validators.required, Validators.minLength(3)]],
			address: ['', [Validators.required, Validators.minLength(3)]]
		});
	}
	ngOnInit(): void {
		this.checkLogin();
		this.getArea();
	}
	checkLogin() {
		//if logging => return home page
		this.store.pipe(takeUntil(this.destroy$), select(customerIsAuthenticatedLoginStateSelector)).subscribe(
			(result) => {
				if (result) {
					this.router.navigate(['/']);
				}
			},
			(error) => {
				this.submitted = false;
				this.appErrorHandler.handleErrorWithNoti(error);
			},
			() => {}
		);
	}
	drop(event: CdkDragDrop<string[]>) {
		//when delete image
		moveItemInArray(this.thumbshandlePreview, event.previousIndex, event.currentIndex);
		console.log(this.fileList);
		const temp = this.fileList[event.previousIndex];
		this.fileList[event.previousIndex] = this.fileList[event.currentIndex];
		this.fileList[event.currentIndex] = temp;
		console.log('after change' + event.previousIndex + 'and' + event.currentIndex);
		console.log(this.fileList);
	}
	// create base64 url
	getBase64(file: File): Promise<string | ArrayBuffer | null> {
		return new Promise((resolve, reject) => {
			const reader = new FileReader();
			reader.readAsDataURL(file);
			reader.onload = () => resolve(reader.result);
			reader.onerror = (error) => reject(error);
		});
	}
	async handlePreview(uid: string, file: File) {
		const fileUrl = await this.getBase64(file);
		if (fileUrl) {
			this.thumbshandlePreview = this.thumbshandlePreview.concat({uid, url: fileUrl.toString()});
		}
	}
	//removeImageFile from thumbshandlePreview
	removeImageFile = (file): boolean => {
		this.thumbshandlePreview.splice(
			this.thumbshandlePreview.findIndex((x) => x.uid === file.uid.toString()),
			1
		);
		this.thumbshandlePreview.find((x) => x.uid === file.uid);
		this.isHaveUploadAvatar = false;
		return true;
	};
	beforeUpload = (file): boolean => {
		this.fileList = this.fileList.concat(file);
		console.log(this.fileList.length);
		//console.log(this.fileList.uid);
		this.handlePreview(file.uid.toString(), file);
		this.isHaveUploadAvatar = true;
		this.ready = true;
		return false;
	};
	initFormData() {
		const val = this.customerRegisterForm.value;
		//console.log(val);
		const formData: FormData = new FormData();
		formData.append('email', val.email);
		formData.append('passWord', val.passWord);
		formData.append('passWordConfirm', val.passWordConfirm);
		formData.append('phoneNumber', val.phoneNumber);
		formData.append('name', val.name);
		formData.append('dateOfBirth', val.dob);
		formData.append('country', 'Việt Nam');
		formData.append('province', this.valuesArea[0]);
		formData.append('city', this.valuesArea[1]);
		formData.append('address', this.valuesArea[2] + ', ' + val.address);
		this.fileList.forEach((file) => {
			formData.append('avatarImages', file, file.name);
		});
		console.log(formData);
		return formData;
	}
	submitForm(): void {
		this.submitted = true; //submitting => dislay spinner
		try {
			this.accountService
				.customerRegister(this.initFormData())
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(result) => {
						if (result) {
							//if result != null
							if (result.message) {
								if (result.isSuccessed) {
									this.notificationService.showSuccessNotification(
										this.translateService.instant(result.message)
									);
									this.router.navigate(['login']);
								} else {
									this.notificationService.showErrorNotification(
										this.translateService.instant(result.message)
									);
									this.submitted = false;
								}
							} else {
							}
						} else {
							this.appErrorHandler.handleErrorWithNoti(
								this.translateService.instant('notification.register.uncommon.common')
							);
							this.submitted = false;
						}
					},
					() => {
						this.submitted = false;
						this.appErrorHandler.handleErrorWithNoti();
					},
					() => {
						this.submitted = false;
					}
				);
		} catch (error) {
			this.appErrorHandler.handleErrorWithNoti(
				this.translateService.instant('notification.register.uncommon.common')
			);
		}
	}
	getArea() {
		this.http.get(this.clientHost + '/assets/area/data.json').subscribe((result: AreaProvinceArray[]) => {
			console.log(result);
			this.areaArray = result.map(function (row) {
				const options1 = row.Districts.map(function (row1) {
					const option2 = row1.Wards.map(function (row2) {
						return {value: row2.Name, label: row2.Name, isLeaf: true};
					});
					return {value: row1.Name, label: row1.Name, children: option2};
				});
				return {value: row.Name, label: row.Name, children: options1};
			});
			console.log(this.areaArray);
		});
	}
	onChanges(values: any): void {
		console.log(values);
		// console.log(this.valuesArea[0]);
		// console.log(this.valuesArea[1]);
		// console.log(this.valuesArea[2]);
	}
}
