import {exhaustMap, map, tap, withLatestFrom} from 'rxjs/operators';
import {CustomerLoginState} from './../login/customer-login.state';
import {Injectable} from '@angular/core';
import {Actions, createEffect, ofType} from '@ngrx/effects';
import {select, Store} from '@ngrx/store';
import {CustomerAccountService} from 'src/app/core/services/auth/customer/customer-account.service';
import {LocalStorageService} from 'src/app/core/services/comon-services/local-storage-service/localstorage.service';
import * as CustomerInforAction from './customer-infor.actions';
import { customerInforStateSelector } from './customer-infor.selector';

export const CUSTOMER_SETTINGS_KEY = 'customerinfor';
@Injectable()
export class CustomerInforEffect {
	loadCustomerInforGet$ = createEffect(() =>
		this.actions$.pipe(
			ofType(CustomerInforAction.customerGetInforAction),
			exhaustMap(() =>
				this.customerAccountService.customerGetCustomerInfor().pipe(
					map((result) => {
						if (result) {
							console.log('result trong effect');
							console.log(result);
							if (result.isSuccessed && result.resultObj) {
								if (result.resultObj && result.message) {
									return CustomerInforAction.customerGetInforSuccessAction({
										customer: result.resultObj,
										message: result.message
									});
								} else {
									return CustomerInforAction.customerGetInforFailedAction({message: ''});
								}
							} else {
								return CustomerInforAction.customerGetInforFailedAction({message: ''});
							}
						} else {
							return CustomerInforAction.customerGetInforFailedAction({message: ''});
						}
					})
				)
			)
		)
	);
	persistSettings = createEffect(
		() =>
			this.actions$.pipe(
				ofType(CustomerInforAction.customerGetInforSuccessAction),
				withLatestFrom(this.store.pipe(select(customerInforStateSelector))),
				tap(([, settings]) => {
					this.localStorageService.setItem(CUSTOMER_SETTINGS_KEY, settings);
				})
			),
		{dispatch: false}
	);
	constructor(
		private actions$: Actions,
		private store: Store<CustomerLoginState>,
		private customerAccountService: CustomerAccountService,
		private localStorageService: LocalStorageService,
		
	) {}
}
