import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {CustomerUrlAdvertisement} from 'src/app/core/utils/URL/customer-url/customer-Advertisement/customer-url-advertisement';
import { ApiResult } from 'src/app/core/models/common/ApiResult';
import { AdvertisementResponse } from 'src/app/core/models/customer/advetisement/response/AdvertisementResponse';

@Injectable({
	providedIn: 'root'
})
export class CustomerAdvertisementService {
	constructor(private http: HttpClient, private customerUrlAdvertisement: CustomerUrlAdvertisement) {}
	getAdvertisements(): Observable<ApiResult<Array<AdvertisementResponse>>> {
		return this.http.get(this.customerUrlAdvertisement.getAdvertisement());
	}
}
