import {Observable} from 'rxjs';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ProductShowHomePageRespond} from 'src/app/core/models/product/respond/ProductShowHomePageRespond';
import {CustomerUrlProductSuggest} from 'src/app/core/utils/URL/customer-url/customer-product-suggest/customer-url-product-suggest';

@Injectable({
	providedIn: 'root'
})
export class SuggestService {
	constructor(private http: HttpClient, private customerUrlProductSuggest: CustomerUrlProductSuggest) {}
	getProductsBySimilarProduct(): Observable<ApiResult<Array<ProductShowHomePageRespond>>> {
		return this.http.get<ApiResult<Array<ProductShowHomePageRespond>>>(
			this.customerUrlProductSuggest.getProductsBySimilarProduct()
		);
	}
	getProductsByCosin(productId: number): Observable<ApiResult<Array<ProductShowHomePageRespond>>> {
		return this.http.get<ApiResult<Array<ProductShowHomePageRespond>>>(
			this.customerUrlProductSuggest.getProductsByCosin(productId)
		);
	}
	getProductsSuggestSameShop(productId: number): Observable<ApiResult<Array<ProductShowHomePageRespond>>> {
		return this.http.get<ApiResult<Array<ProductShowHomePageRespond>>>(
			this.customerUrlProductSuggest.getProductsSuggestSameShop(productId)
		);
	}
}
