import {HttpParams} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import {RequestBase} from 'src/app/core/models/common/request/RequestBase';
import {OnSearchRequest} from 'src/app/core/models/product/request/OnSearchRequest';
import {CustomerUrlBase} from './../customer-url-base';
@Injectable({
	providedIn: 'root'
})
export class CustomerUrlProduct extends CustomerUrlBase {
	constructor() {
		super();
	}
	customerGetProductAPI(pagingRequestBase: PagingRequestBase): string {
		let params = new HttpParams()
			.set('pageindex', pagingRequestBase.pageIndex == undefined ? '1' : pagingRequestBase.pageIndex.toString())
			.set('pagesize', pagingRequestBase.pageSize == undefined ? '10' : pagingRequestBase.pageSize.toString())
			.set('sortby', pagingRequestBase.sortby == undefined ? '' : pagingRequestBase.sortby);
		return this.customerUrl + '/product/products?' + params;
	}
	customerGetProductDetailByIdAPI(requestBase: RequestBase): string {
		return this.customerUrl + `/product/product/${requestBase.id}/${requestBase.languageCode}`;
	}
	customerGetProductByCategoryVaritionAliasAPI(
		pagingRequestBase: PagingRequestBase,
		categoryVariationAlias: string
	): string {
		let params = new HttpParams()
			.set('pageindex', pagingRequestBase.pageIndex == undefined ? '1' : pagingRequestBase.pageIndex.toString())
			.set('pagesize', pagingRequestBase.pageSize == undefined ? '10' : pagingRequestBase.pageSize.toString())
			.set('sortby', pagingRequestBase.sortby == undefined ? '' : pagingRequestBase.sortby)
			.set('languageCode', pagingRequestBase.languageCode == undefined ? '' : pagingRequestBase.languageCode);
		return this.customerUrl + '/product/categoryVariation/' + categoryVariationAlias + '?' + params.toString();
	}
	customerGetProductBySearchKeyWordAPI(onSearchRequest: OnSearchRequest): string {
		let params = new HttpParams()
			.set('type', onSearchRequest.type == undefined ? 'book' : onSearchRequest.type.toString())
			.set('keyword', onSearchRequest.keyWord == undefined ? '' : onSearchRequest.keyWord.toString())
			.set('pageindex', onSearchRequest.pageIndex == undefined ? '1' : onSearchRequest.pageIndex.toString())
			.set('pagesize', onSearchRequest.pageSize == undefined ? '10' : onSearchRequest.pageSize.toString())
			.set('sortby', onSearchRequest.sortby == undefined ? '' : onSearchRequest.sortby);
		console.log(this.customerUrl + `/product/search?` + params.toString());
		return this.customerUrl + `/product/search?` + params.toString();
	}
	getProductCommentProgress(productId: number): string {
		return this.customerUrl + `/product/comment-progresses/` + productId;
	}
	getGroupProducts(productId: number) {
		return this.customerUrl + `/product/group-products/` + productId;
	}
	getGroupProduct(pagingRequestBase: PagingRequestBase): string {
		let params = new HttpParams()
			.set('pageindex', pagingRequestBase.pageIndex == undefined ? '1' : pagingRequestBase.pageIndex.toString())
			.set('pagesize', pagingRequestBase.pageSize == undefined ? '10' : pagingRequestBase.pageSize.toString())
			.set('id', pagingRequestBase.id == undefined ? '1' : pagingRequestBase.id.toString());
		return this.customerUrl + '/product/group-product?' + params;
	}
}
