import {ApiResult} from './../../../models/common/ApiResult';
import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ApiResponseMessage} from 'src/app/core/models/common/ApiResponseMessage';
import {Router} from '@angular/router';
import {CustomerUrlCart} from 'src/app/core/utils/URL/customer-url/customer-cart/customer-url-cart';
import {CartResponse} from 'src/app/core/models/cart/cart-response.viewmodel';
interface AddToCartRequest {
	productId: number;
	quantity: number;
	productType: number; //1 là sp 2 là nhóm sp
}
@Injectable({
	providedIn: 'root'
})
export class CartService {
	constructor(private http: HttpClient, private customerUrlCartString: CustomerUrlCart, public router: Router) {}

	addToCart(productId: number, quantity: number, type: number): Observable<ApiResponseMessage> {
		let obj: AddToCartRequest = {
			productId: productId,
			quantity: Number(quantity),
			productType: type
		};
		console.log(obj);
		return this.http.post<ApiResponseMessage>(this.customerUrlCartString.customerPostAddToCart(), obj);
	}
	buyNow(productId: number): Observable<ApiResponseMessage> {
		return this.http.post<ApiResponseMessage>(this.customerUrlCartString.customerPostBuyNow(), {productId});
	}
	getCartProductCount(): Observable<ApiResult<number>> {
		return this.http.get<ApiResult<number>>(this.customerUrlCartString.getCartProductCount());
	}
	changeQuantity(productId: number, quantity: number, productType: number): Observable<ApiResponseMessage> {
		return this.http.post<ApiResponseMessage>(this.customerUrlCartString.customerPostChangeQuantity(), {
			productId,
			quantity,
			productType
		});
	}

	deleteCardProduct(productId: number, productType: number) {
		return this.http.delete<ApiResponseMessage>(
			this.customerUrlCartString.customerDeleteCardProduct(productId, productType)
		);
	}
	order() {
		return this.http.get<ApiResponseMessage>(this.customerUrlCartString.customerOrder());
	}
	getCart(): Observable<ApiResult<CartResponse>> {
		console.log('trong ');
		return this.http.get<ApiResult<CartResponse>>(this.customerUrlCartString.customerGetCart());
	}
	deleteCart(): Observable<ApiResult<boolean>> {
		return this.http.delete<ApiResult<boolean>>(this.customerUrlCartString.deleteCart());
	}
}
