
import { CustomerUrlAccount } from './../../../utils/URL/customer-url/customer-account/customer-url-account';
import {Observable} from 'rxjs';
import {ApiResult} from '../../../models/common/ApiResult';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Customer} from 'src/app/core/models/customer/customer.model';
import {CustomerLogin} from 'src/app/core/models/customer/login/request/customer-login.viewmodel';
import {CustomerInforResponse} from 'src/app/core/models/customer/customer/response/CustomerInforResponse';
import {ImageUploadPreviewResponse} from 'src/app/core/models/product/respond/ImageUploadPreviewResponse.viewmodel';
import {ProfileUpdateRequest} from 'src/app/core/models/customer/customer/request/ProfileUpdateRequest';
import { ComfirmPasswordRequest } from 'src/app/core/models/customer/customer/request/ComfirmPasswordRequest';
import { ChangePasswordRequest } from 'src/app/core/models/customer/customer/request/ChangePasswordRequest';
@Injectable({
	providedIn: 'root'
})
export class CustomerAccountService {
	constructor(private http: HttpClient, private customerUrlAccountString: CustomerUrlAccount) {
		//this.user = this.adminSubject.asObservable();
	}
	customerlogin(customerLogin: CustomerLogin) {
		return this.http.post<ApiResult<Customer>>(
			this.customerUrlAccountString.customerUrlLogin,
			{
				email: customerLogin.email,
				passWord: customerLogin.passWord,
				remember: customerLogin.remember,
				urlReturn: customerLogin.urlReturn
			},
			{
				responseType: 'json'
			}
		);
	}
	customerRegister(customerRegisterForm: FormData):Observable<ApiResult<boolean>> {
		return this.http.post<ApiResult<boolean>>(this.customerUrlAccountString.customerUrlRegister, customerRegisterForm);
	}

	customerUpdateProfile(request: ProfileUpdateRequest): Observable<ApiResult<boolean>> {
		return this.http.put<ApiResult<boolean>>(this.customerUrlAccountString.customerUpdateProfile(), request);
	}

	customerGetCustomerInfor() {
		return this.http.get<ApiResult<CustomerInforResponse>>(this.customerUrlAccountString.customerGetCustomerInforAPI());
	}
	postAvatarImagePreview(newProductImage: FormData):Observable<ApiResult<ImageUploadPreviewResponse>> {
		return this.http.post<ApiResult<ImageUploadPreviewResponse>>(
			this.customerUrlAccountString.customerPostAvatarImagePreviewAPI(),
			newProductImage
		);
	}
	confirmPassWord(passWord: ComfirmPasswordRequest):Observable<ApiResult<ImageUploadPreviewResponse>> {
		return this.http.post<ApiResult<ImageUploadPreviewResponse>>(
			this.customerUrlAccountString.customerPostConfirmPassWordAPI(),
			passWord
		);
	}
	customerChangePassword(changePasswordRequest:ChangePasswordRequest):Observable<ApiResult<boolean>> {
		return this.http.post<ApiResult<boolean>>(
			this.customerUrlAccountString.customerPostChangePasswordAPI(),
			changePasswordRequest
		);
	}
}
