import {Component, OnInit} from '@angular/core';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {ProductShowHomePageRespond} from 'src/app/core/models/product/respond/ProductShowHomePageRespond';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {ProductService} from 'src/app/core/services/customer/product/product.service';
import {ActivatedRoute} from '@angular/router';
import {SortProductService} from 'src/app/core/services/comon-services/sort-product/sort-product.service';
import {concatMap} from 'rxjs/operators';

@Component({
	selector: 'app-home-page',
	templateUrl: './home-page.component.html',
	styleUrls: ['./home-page.component.less'],
	providers: [DestroyService]
})
export class HomePageComponent implements OnInit {
	isLoading = false; //skelton
	apiResult: ApiResult<PagedResult<ProductShowHomePageRespond>>; //result will be passed to index component
	constructor(
		private productService: ProductService,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler,
		private route: ActivatedRoute,
		private sortProductService: SortProductService 
	) {
		this.apiResult = {}; //init 
	}
	ngOnInit(): void {
		this.getQuery();
	}
	getQuery() {
		this.sortProductService
			.getQuery(this.route)
			.pipe(
				takeUntil(this.destroy$),
				concatMap((value) => {
					this.isLoading = true;
					return this.productService.getProduct({
						pageIndex: value.page == undefined ? 1 : value.page,
						pageSize: 10,
						sortby: value.sortBy,
						order: value.order
					});
				})
			)
			.subscribe(
				(result) => {
					this.apiResult = {};
					this.apiResult = result;
					this.isLoading = false;
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				},
				() => {}
			);
	}
}
