import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {SortProduct} from 'src/app/core/models/product/SortProduct';
import {Component, Input, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {ProductShowHomePageRespond} from 'src/app/core/models/product/respond/ProductShowHomePageRespond';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SortProductService} from 'src/app/core/services/comon-services/sort-product/sort-product.service';
import {environment} from 'src/environments/environment';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';

@Component({
	selector: 'app-customer-index',
	templateUrl: './index.component.html',
	styleUrls: ['./index.component.less'],
	providers: [DestroyService]
})
export class IndexComponent implements OnInit {
	@Input() apiResult: ApiResult<PagedResult<ProductShowHomePageRespond>> = {}; //result will be received
	sortSelected: SortProduct = {}; //combo of sort
	radioValue: string = ''; //for sortBy
	productShowHomePageResponds: Array<ProductShowHomePageRespond> = []; // products will be displayed
	host: string = environment.apiUrl; //host of BE
	constructor(
		private router: Router,
		private route: ActivatedRoute,
		private sortProductService: SortProductService,
		private noti: NotificationService,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler,
		private spinner: SpinnerService,
	
	) {}
	ngOnInit(): void {
		this.sortProductService
			.getQuery(this.route) //take query from url
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					this.sortSelected = result;
					if (result.sortBy) {
						this.radioValue = result.sortBy;
					}
				},
				() => {
					this.appErrorHandler.handleErrorWithNoti();
				}
			);
		if (this.apiResult.isSuccessed) {
			if (this.apiResult.resultObj) {
				if (this.apiResult.resultObj.items) {
					this.productShowHomePageResponds = this.apiResult.resultObj.items;
					console.log(this.productShowHomePageResponds);
					this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
				} else {
					this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
				}
			} else {
				if (this.apiResult.message) {
					this.noti.showErrorNotification(this.apiResult.message);
				}
				this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
			}
		} else {
			if (this.apiResult.message) {
				this.noti.showErrorNotification(this.apiResult.message);
			}
			this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
		}
	}
	getRadioSelect(sortBy: string) {
		this.sortSelected.sortBy = sortBy; //received by click on sort
		this.sortSelected.page = 1; // return page 1
		this.getProductByUrlType(); // navigated by sort and page
	}

	pageChanged(page: number) {
		this.sortSelected.page = page; // receive the page number
		this.sortSelected.sortBy = this.sortSelected.sortBy; // display the sort button by previous sort
		this.getProductByUrlType(); // navigated by sort and page
	}
	getProductByUrlType() {
		this.router.navigate([], {queryParams: this.sortSelected}); //navigate
	}
}
