import {Component, OnInit} from '@angular/core';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {AdvertisementResponse} from 'src/app/core/models/customer/advetisement/response/AdvertisementResponse';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {CustomerAdvertisementService} from 'src/app/core/services/customer/Advertisement/customer-advertisement.service';
import {environment} from 'src/environments/environment';
@Component({
	selector: 'app-customer-carousel',
	templateUrl: './customer-carousel.component.html',
	styleUrls: ['./customer-carousel.component.less'],
	providers: [DestroyService]
})
export class CustomerCarouselComponent implements OnInit {
	advertisementResponses: Array<AdvertisementResponse> = [];
	isLoading: boolean = false;
	host: string = environment.apiUrl; //host of BE
	constructor(
		private customerAdvertisementService: CustomerAdvertisementService,
		private appErrorHandler: AppErrorHandler,
		private destroy$: DestroyService
	) {}
	ngOnInit(): void {
		this.getAdvertisements();
	}
	getAdvertisements() {
		this.isLoading = true;
		this.customerAdvertisementService
			.getAdvertisements()
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						console.log(result);
						if (result.isSuccessed) {
							if (result.resultObj) {
								this.advertisementResponses = result.resultObj;
							}
						}
					}
					this.isLoading = false;
				},
				() => {
					this.appErrorHandler.handleErrorWithNoti();
				}
			);
	}
}
