import {CartComponent} from './components/customer-body/cart/cart.component';
import {ProductDetailComponent} from './components/customer-body/product-detail/product-detail.component';
import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {CustomerComponent} from './customer.component';
import {CustomerAuthGuard} from 'src/app/core/guards/customer/customer-auth.guard';
import {HomePageComponent} from './components/customer-body/home-page/home-page.component';
import {CategoryVariationComponent} from './components/customer-body/category-variation/category-variation.component';
import {CustomerLoginGuard} from 'src/app/core/guards/customer/login-guard/customer-login.guard';
const routes: Routes = [
	{
		path: '',
		component: CustomerComponent,
		children: [
			{
				path: '',
				component: HomePageComponent,
				data: {
					title: 'Customer-app.body.home.title'
				},
				children: []
			},
			{
				path: 'collections/:alias',
				component: CategoryVariationComponent,
				data: {
					title: 'Customer-app.body.home.title',
					breadcrumb: 'Collection'
				}
			},
			{
				path: 'products/:productId',
				component: ProductDetailComponent,
				data: {
					title: 'Customer-app.body.product-detail.title',
					breadcrumb: 'Book'
				}
			},
			{
				path: 'cart',
				component: CartComponent,
				canActivate: [CustomerAuthGuard],
				data: {
					title: 'Customer-app.body.cart.title',
					breadcrumb: 'Cart'
				}
			},
			{
				path: 'country',
				loadChildren: () =>
					import('./components/customer-body/modules/country/country.module').then((m) => m.CountryModule)
			},
			{
				path: 'publisher',
				loadChildren: () =>
					import('./components/customer-body/modules/publisher/publisher.module').then((m) => m.PublisherModule)
			},
			{
				path: 'author',
				loadChildren: () =>
					import('./components/customer-body/modules/customer-author/customer-author.module').then((m) => m.CustomerAuthorModule)
			},
			{
				path: 'group-product',
				loadChildren: () =>
					import('./components/customer-body/modules/customer-group-product/customer-group-product.module').then((m) => m.CustomerGroupProductModule)
			},

			{
				path: 'shop',
				loadChildren: () =>
					import('./components/customer-body/modules/shop/shop.module').then((m) => m.ShopModule)
			},
			{
				path: 'chat',
				loadChildren: () =>
					import('./components/customer-body/modules/chat/Customer-chat.module').then((m) => m.CustomerChatModule)
			},
			{
				path: 'user',
				canActivate: [CustomerAuthGuard],
				loadChildren: () =>
					import('./components/customer-body/modules/user/user.module').then((m) => m.UserModule)
			}
		]
	},
	{
		path: 'password',
		canActivate: [CustomerLoginGuard],
		loadChildren: () =>
			import('./components/customer-body/modules/customer-reset-password/customer-reset-password.module').then(
				(m) => m.CustomerResetPasswordModule
			)
		//canActivate: [AuthGuard],
	},
	{
		path: 'login',
		canActivate: [CustomerLoginGuard],
		loadChildren: () => import('./module/customer-login/customer-login.module').then((m) => m.CustomerLoginModule)
		//canActivate: [AuthGuard] ,
	},
	{
		path: 'register',
		canActivate: [CustomerLoginGuard],
		loadChildren: () =>
			import('./module/customer-register/customer-register.module').then((m) => m.CustomerRegisterModule)
	},
	{
		path: 'notfound',
		loadChildren: () =>
			import('./module/customer-notfound/customer-notfound.module').then((m) => m.CustomerNotfoundModule)
	},
	{path: '**', redirectTo: 'notfound'}
];
@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class CustomerRoutingModule {}