import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import {RequestBase} from 'src/app/core/models/common/request/RequestBase';
import { GroupProductPagingViewModel, GroupProductViewModel } from 'src/app/core/models/common/respond/ProductRespondBase';
import { OnSearchRequest } from 'src/app/core/models/product/request/OnSearchRequest';
import {BookDetailRespond} from 'src/app/core/models/product/respond/BookDetailRespond';
import {ProductShowHomePageRespond} from 'src/app/core/models/product/respond/ProductShowHomePageRespond';
import {CustomerUrlProduct} from 'src/app/core/utils/URL/customer-url/customer-product/customer-url-product';
@Injectable({
	providedIn: 'root'
})
export class ProductService {
	languageCode = 'vn';
	categoryId = 1;
	constructor(private http: HttpClient, private customerUrlProductString: CustomerUrlProduct) {}
	getProductByCategoryVaritionAlias(pagingRequestBase: PagingRequestBase, alias: string) {
		pagingRequestBase.languageCode = this.languageCode;
		return this.http.get<ApiResult<PagedResult<ProductShowHomePageRespond>>>(
			this.customerUrlProductString.customerGetProductByCategoryVaritionAliasAPI(pagingRequestBase, alias)
		);
	}
	getProduct(pagingRequestBase: PagingRequestBase) {
		return this.http.get<ApiResult<PagedResult<ProductShowHomePageRespond>>>(
			this.customerUrlProductString.customerGetProductAPI(pagingRequestBase)
		);
	}
	getProductById(requestBase: RequestBase) {
		return this.http.get<ApiResult<BookDetailRespond>>(
			this.customerUrlProductString.customerGetProductDetailByIdAPI(requestBase)
		);
	}
	getProductBySearch(onSearchRequest: OnSearchRequest) {
		return this.http.get<ApiResult<PagedResult<ProductShowHomePageRespond>>>(
			this.customerUrlProductString.customerGetProductBySearchKeyWordAPI(onSearchRequest)
		);
	}
	getGroupProducts(productId:number) {
		return this.http.get<ApiResult<Array<GroupProductViewModel>>>(
			this.customerUrlProductString.getGroupProducts(productId)
		);
	}
	getGroupProduct(pagingRequestBase: PagingRequestBase) {
		return this.http.get<ApiResult<GroupProductPagingViewModel>>(
			this.customerUrlProductString.getGroupProduct(pagingRequestBase)
		);
	}
}
