import {TranslateService} from '@ngx-translate/core';
import {exhaustMap, tap, withLatestFrom} from 'rxjs/operators';
import {Actions, createEffect, ofType} from '@ngrx/effects';
import * as LoginActions from './customer-login.actions';
import * as CustomerInforActions from '../infor/customer-infor.actions';
import {Injectable} from '@angular/core';
import {CustomerAccountService} from 'src/app/core/services/auth/customer/customer-account.service';
import {map} from 'rxjs/internal/operators/map';
import {select, Store} from '@ngrx/store';
import {LocalStorageService} from 'src/app/core/services/comon-services/local-storage-service/localstorage.service';
import {CustomerAppState} from '../customer-app.state';
import {customerLoginStateSelector} from './customer-login.selector';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
export const CUSTOMER_SETTINGS_KEY = 'customerlogin';
@Injectable()
export class CustomerLoginEffect {
	loadCustomerLogin$ = createEffect(() =>
		this.actions$.pipe(
			ofType(LoginActions.customerLoginAction),
			exhaustMap((action) =>
				this.customerAccountService.customerlogin(action.customerLogin).pipe(
					map((result) => {
						//check have obj
						if (result.isSuccessed && result.resultObj) {
							//if login successfully
							if (result.message) {
								this.notificationService.showSuccessNotification(
									this.translateService.instant(result.message)
								);
								if (result.resultObj.tokenValue) {
									//	if (result.resultObj.remember) {
									this.localStorageService.removeItem('customer-token');
									this.localStorageService.setItem('customer-token', result.resultObj.tokenValue);
									//CustomerInforActions.customerGetInforAction();
									// this.appCookieService.remove('customer-token');
									// this.appCookieService.set(
									// 	'customer-token',
									// 	result.resultObj.tokenValue,
									// 	parseInt(
									// 		this.jwtTokenService.getExpiryTime(result.resultObj.tokenValue),
									// 		10
									// 	)
									// );
									// } else {
									// 	this.appCookieService.remove('customer-token');
									// 	this.appCookieService.set('customer-token', result.resultObj.tokenValue);
									// }
									this.store.dispatch(CustomerInforActions.customerGetInforAction());
									return LoginActions.customerLoginSuccessAction({
										message: result.message
									});
								} else {
									return LoginActions.customerLoginFailedAction({message: result.message});
								}
							} else {
								this.notificationService.showSuccessNotification(
									this.translateService.instant('notification.login.common.successfully')
								);
								return LoginActions.customerLoginSuccessAction({
									message: 'notification.login.common.successfully'
								});
							}
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(
									this.translateService.instant(result.message)
								);
							}
							return LoginActions.customerLoginFailedAction({message: result.message});
						}
					})
				)
			)
		)
	);
	loadCustomerResetLogin$ = createEffect(() =>
		this.actions$.pipe(
			ofType(LoginActions.customerLoginResetAction),
			map(() => {
				this.localStorageService.removeItem('customerlogin');
				this.localStorageService.removeItem('customerinfor');
				this.localStorageService.removeItem('customer-token');
			
				return LoginActions.customerLogoutSuccessAction({message: 'logout'});
			})
		)
	);
	persistSettings = createEffect(
		() =>
			this.actions$.pipe(
				ofType(LoginActions.customerLoginSuccessAction),
				withLatestFrom(this.store.pipe(select(customerLoginStateSelector))),
				tap(([, settings]) => {
					this.localStorageService.setItem(CUSTOMER_SETTINGS_KEY, settings);
				})
			),
		{dispatch: false}
	);
	constructor(
		private actions$: Actions,
		private store: Store<CustomerAppState>,
		private customerAccountService: CustomerAccountService,
		private localStorageService: LocalStorageService,
		private notificationService: NotificationService,
		private translateService: TranslateService
	) {}
}
