import {Injectable} from '@angular/core';
import {CustomerUrlBase} from './../customer-url-base';
@Injectable({
	providedIn: 'root'
})
export class CustomerUrlAccount extends CustomerUrlBase {
	readonly customerUrlLogin: string = this.customerUrl + '/customer/login';
	readonly customerUrlRegister: string = this.customerUrl + '/customer/register';

	customerRegisterRequestAPI(): string {
		return this.customerUrl + 'register';
	}
	customerUpdateProfile() {
		return this.customerUrl + '/customer';
	}
	customerPostAvatarImagePreviewAPI(): string {
		return this.customerUrl + `/customer/image-preview`;
	}
	customerPostConfirmPassWordAPI(): string {
		return this.customerUrl + `/customer/confirm-password`;
	}
	customerGetCustomerInforAPI(): string {
		return this.customerUrl + '/customer';
	}
	customerPostChangePasswordAPI(): string {
		return this.customerUrl + '/customer/change-password';
	}
}
