import { Observable } from 'rxjs';
import { CustomerUrlProduct } from './../../../../core/utils/URL/customer-url/customer-product/customer-url-product';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ApiResult} from 'src/app/core/models/common/ApiResult';

import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import { RequestBase } from 'src/app/core/models/common/request/RequestBase';
import {BookDetailRespond} from 'src/app/core/models/product/respond/BookDetailRespond';
import {ProductShowHomePageRespond} from 'src/app/core/models/product/respond/ProductShowHomePageRespond';
import { CommentProgressProductResponse } from 'src/app/core/models/product/respond/CommentProgressProductResponse';

@Injectable({
	providedIn: 'root'
})
export class IndexService {
	constructor(private http: HttpClient, private urlProductString: CustomerUrlProduct) {}
	getProduct(pagingRequestBase: PagingRequestBase):Observable<ApiResult<PagedResult<ProductShowHomePageRespond>>> {
		return this.http.get<ApiResult<PagedResult<ProductShowHomePageRespond>>>(
			this.urlProductString.customerGetProductAPI(pagingRequestBase)
		);
	}
	getProductById(requestBase: RequestBase):Observable<ApiResult<BookDetailRespond>> {
		return this.http.get<ApiResult<BookDetailRespond>>(
			this.urlProductString.customerGetProductDetailByIdAPI(requestBase)
		);
	}
	
	getProductCommentProgress(productId:number):Observable<ApiResult<Array<CommentProgressProductResponse>>> {
		return this.http.get<ApiResult<Array<CommentProgressProductResponse>>>(
			this.urlProductString.getProductCommentProgress(productId)
		);
	}
}
