import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {takeUntil} from 'rxjs/operators';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
@Component({
	selector: 'app-customer',
	templateUrl: './customer.component.html',
	styleUrls: ['./customer.component.less', './customer.component.scss'],
	providers: [DestroyService],
	encapsulation: ViewEncapsulation.None
})
export class CustomerComponent implements OnInit {
	isSubmited: boolean;
	isNotHaveData: boolean;
	constructor(
		private appErrorHandler: AppErrorHandler,
		private customerSpinnerService: SpinnerService,
		private destroyService$: DestroyService
	) {
		this.isSubmited = false;
		this.isNotHaveData = false;
	}
	ngOnInit(): void {
		this.controlSpinner();
	}
	controlSpinner() {
		this.customerSpinnerService.subjectSubmitLoadingHaveData$.pipe(takeUntil(this.destroyService$)).subscribe(
			(result) => {
				console.log(result);
				this.isSubmited = result.isSubmitted;
				//this.isNotHaveData = result.isNotHaveData;
			},
			(error) => {
				this.appErrorHandler.handleErrorWithNoti(error);
			}
		);
	}
}
