import {SortProduct} from '../../../models/product/SortProduct';
import {Injectable} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {map} from 'rxjs/operators';
import {Observable} from 'rxjs';

@Injectable({
	providedIn: 'root'
})
export class SortProductService {
	sortSelected: SortProduct;
	constructor() {
		this.sortSelected = {};
	}
	getQuery(route: ActivatedRoute): Observable<SortProduct> {
		return route.queryParams.pipe(
			map((params) => {
				let sortSelected: SortProduct = {};
				if (params['order']) {
					sortSelected = {order: params['order']};
				}
				if (params['page']) {
					if (typeof +params['page'] == 'string') {
						sortSelected.page = 1;
					} else {
						sortSelected.page = +params['page'];
					}
				}
				if (params['sortBy']) {
					switch (params['sortBy']) {
						case 'pop':
							sortSelected.sortBy = 'pop';
							break;
						case 'ctime':
							sortSelected.sortBy = 'ctime';
							break;
						case 'sales':
							sortSelected.sortBy = 'sales';
							break;
						case 'high':
							sortSelected.sortBy = 'high';
							break;
						case 'low':
							sortSelected.sortBy = 'low';
							break;
						default:
							sortSelected.sortBy = 'pop';
							break;
					}
				}
				return sortSelected;
			})
		);
	}
}
