import { ProductSuggestModule } from './../modules/product/product-suggest/product-suggest.module';
import { CustomerInforEffect } from './../../core/store/customer/infor/customer-infor.effects';
import {CustomerLoginEffect} from './../../core/store/customer/login/customer-login.effects';
/* eslint-disable @typescript-eslint/naming-convention */
/*SYSTEM */
import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HttpClient, HttpClientModule} from '@angular/common/http'; //
import {TranslateHttpLoader} from '@ngx-translate/http-loader'; //
/**TRANSLATION */
import {TranslateLoader, TranslateModule} from '@ngx-translate/core'; //
/**GUI */
import {MaterialModule} from 'src/app/core/utils/material.module';
import {NgZorroAntdModule} from 'src/app/core/utils/ng-zorro-antd/ng-zorro-antd.module';
/**COMPONENT */
import {CustomerHeaderComponent} from './components/customer-header/customer-header.component';
import {CustomerBodyComponent} from './components/customer-body/customer-body.component';
import {CustomerLoginModule} from './module/customer-login/customer-login.module';
import {CustomerRegisterModule} from './module/customer-register/customer-register.module';
import {CustomerProductComponent} from './components/customer-body/components/customer-product/customer-product.component';
import {ProductDetailComponent} from './components/customer-body/product-detail/product-detail.component';
import {CartComponent} from './components/customer-body/cart/cart.component';
import {CustomerRoutingModule} from './customer-routing.module';
import {CustomerComponent} from './customer.component';

/**NZ-ANT */
import {IconsProviderModule} from './icons-provider.module';
import {NzLayoutModule} from 'ng-zorro-antd/layout';
import {NzMenuModule} from 'ng-zorro-antd/menu';
import {EffectsModule} from '@ngrx/effects';
import {CustomerSettingsEffects} from 'src/app/core/store/customer/setting/customer-settings.effects';
import {CustomerSideBarComponent} from './components/customer-body/components/customer-side-bar/customer-side-bar.component';
import {CategoryVariationComponent} from './components/customer-body/category-variation/category-variation.component';
import {HomePageComponent} from './components/customer-body/home-page/home-page.component';
import {CustomerFooterComponent} from './components/customer-footer/customer-footer.component';
import {CustomerIndexModule} from './components/customer-body/components/customer-index/customer-index.module';
import { NotificationModule } from '../modules/notification/notification.module';
import { CustomerNotificationComponent } from './components/customer-header/customer-notification/customer-notification.component';
import { OrderDetailSelectModalModule } from '../modules/order/order-detail-select-modal/order-detail-select-modal.module';
import { ChatModule } from '../modules/chat/chat.module';
//export const httpLoaderFactory=(http: HttpClient) => new TranslateHttpLoader(http); //

@NgModule({
	declarations: [
		CustomerComponent,
		CustomerHeaderComponent,
		CustomerBodyComponent,
		CustomerProductComponent,
		ProductDetailComponent,
		CartComponent,
		CustomerSideBarComponent,
		CategoryVariationComponent,
		HomePageComponent,
		CustomerFooterComponent,
		//CustomerCarouselComponent,
		CustomerNotificationComponent
	],
	imports: [
		CommonModule,
		ReactiveFormsModule,
		NgZorroAntdModule,
		CustomerRoutingModule, //sai vì imort sai thứ tự, phải routing trước mới đến module
		CustomerLoginModule,
		CustomerRegisterModule,
		IconsProviderModule,
		NzLayoutModule,
		NzMenuModule,
		FormsModule,
		CustomerIndexModule,
		NotificationModule,
		ChatModule,
		HttpClientModule,
		MaterialModule,
		ProductSuggestModule,
		OrderDetailSelectModalModule,
		TranslateModule.forRoot({
			loader: {
				provide: TranslateLoader,
				useFactory: HttpLoaderFactory,
				deps: [HttpClient]
			}
		}),
		// StoreModule.forFeature("feature_core", reducers, {metaReducers}),

		// EffesctsModule.forFeature([LoginEffects]),
		EffectsModule.forFeature([CustomerSettingsEffects]),
		EffectsModule.forFeature([CustomerLoginEffect]),
		EffectsModule.forFeature([CustomerInforEffect]),
	
		// StoreModule.forFeature('customerinfor', customerInforReducer),
		// StoreModule.forFeature('customerlogin', customerLoginReducer),
		// StoreModule.forFeature('customersettings', customerSettingReducer)
		// InfiniteScrollModule,
	],
	bootstrap: [CustomerComponent]
})
export class CustomerModule {}
export function HttpLoaderFactory(http: HttpClient) {
	return new TranslateHttpLoader(http);
}
