import {Injectable} from '@angular/core';
import {CustomerUrlBase} from './../customer-url-base';
@Injectable({
	providedIn: 'root'
})
export class CustomerUrlRating extends CustomerUrlBase {
	getUserRating(productId: number): string {
		return this.customerUrl + '/rating/' + productId;
	}
	putChangeRating(): string {
		return this.customerUrl + '/rating';
	}
}
