import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ApiResultWithList } from 'src/app/core/models/common/ApiResultWithList';
import { CategoryVariationWithAliasResponseVM } from 'src/app/core/models/seller/category/categoryVariationWithAliasResponse.ViewModel';
import { CustomerUrlCategory } from 'src/app/core/utils/URL/customer-url/customer-category/customer-url-category';

@Injectable({
  providedIn: 'root'
})
export class CategoryService {
  languageCode = 'vn';
	categoryId = 1;
  constructor(private http: HttpClient, private customerUrlCategoryString:CustomerUrlCategory) { }
  getCategoryVariationList(): Observable<ApiResultWithList<CategoryVariationWithAliasResponseVM>> {
		return this.http.get<ApiResultWithList<CategoryVariationWithAliasResponseVM>>(
			this.customerUrlCategoryString.customerGetCategoryVariationByCatgoryIdAPI(
				{languageCode: this.languageCode},
				this.categoryId
			)
		);
	}
}
