import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
import {CartService} from './../../../../../core/services/customer/cart/cart.service';
import {Component, OnInit} from '@angular/core';
import {CartShopResponse} from 'src/app/core/models/cart/cart-shop-response.viewmodel';
import {environment} from 'src/environments/environment';
import {takeUntil} from 'rxjs/operators';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {BehaviorSubject} from 'rxjs';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {Observable} from 'rxjs/internal/Observable';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {ProductShowHomePageRespond} from 'src/app/core/models/product/respond/ProductShowHomePageRespond';
import {SuggestService} from 'src/app/core/services/customer/suggest/suggest.service';
import {AuthService} from 'src/app/core/services/auth/auth.service';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
@Component({
	selector: 'app-cart',
	templateUrl: './cart.component.html',
	styleUrls: ['./cart.component.less'],
	providers: [DestroyService]
})
export class CartComponent implements OnInit {
	isLoading: boolean = false; //  //skelton
	isLogin = false;
	cartShopResponses: Array<CartShopResponse>; //the result is specificed by shop
	totalProductQuantity = 0;
	totalProductPrice = 0;
	cartShopResponses$ = new BehaviorSubject<Array<CartShopResponse>>([]);
	totalProductPrice$: BehaviorSubject<number>;
	isProductSuggestionProductSimilarLoading: boolean = false;
	isProductSuggestionProductCosinLoading: boolean = false;
	apiResultProductShowHomePageResponds?: ApiResult<Array<ProductShowHomePageRespond>>;
	apiResultProductSuggestCosin?: ApiResult<Array<ProductShowHomePageRespond>>;
	host: string = environment.apiUrl;
	constructor(
		private cartService: CartService,
		private destroy$: DestroyService,
		private spinner: SpinnerService,
		private noti: NotificationService,
		private appErrorHandler: AppErrorHandler,
		private suggestService: SuggestService,
		private authService: AuthService
	) {
		this.cartShopResponses = [];
	}
	ngOnInit(): void {
		this.getCart();
		this.changeTotalProductQuantityAndPrice();
		this.checkLogin();
	}
	getCart() {
		this.isLoading = true; //turn on skeleton
		this.cartService
			.getCart() //get cart form service
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						if (result.isSuccessed) {
							if (result.message) {
								this.noti.showSuccessNotification(result.message);
							}
							if (result.resultObj) {
								if (result.resultObj.cartShopResponses) {
									this.cartShopResponses = result.resultObj.cartShopResponses;
									console.log(this.cartShopResponses);
									this.cartShopResponses$.next(this.cartShopResponses);
									this.spinner.subjectSubmitLoadingHaveData$.next({
										isSubmitted: false
									});
								} else {
									this.spinner.subjectSubmitLoadingHaveData$.next({
										isSubmitted: false
									});
								}
							} else {
								this.spinner.subjectSubmitLoadingHaveData$.next({
									isSubmitted: false
								});
							}
						} else {
							if (result.message) {
								this.noti.showErrorNotification(result.message);
							}
							this.spinner.subjectSubmitLoadingHaveData$.next({
								isSubmitted: false
							});
						}
						this.isLoading = false;
					} else {
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	changeTotalProductQuantityAndPrice() {
		this.cartShopResponses$.pipe(takeUntil(this.destroy$)).subscribe(
			(result) => {
				if (result) {
					this.totalProductQuantity = 0; //calculate the quantity again
					this.totalProductPrice = 0; //calculate the price again
					result.forEach((value) => {
						if (value.cartProductResponses) {
							value.cartProductResponses.forEach((product) => {
								if (product.quantity && product.price) {
									this.totalProductQuantity = this.totalProductQuantity + product.quantity;
									this.totalProductPrice = this.totalProductPrice + product.quantity * product.price;
								} else {
								}
							});
						} else {
						}
						if (value.cartGroupProductResponses) {
							value.cartGroupProductResponses.forEach((product) => {
								console.log(product);
								if (product.quantity && product.price) {
									this.totalProductQuantity = this.totalProductQuantity + product.quantity;
									this.totalProductPrice = this.totalProductPrice + product.quantity * product.price;
								} else {
								}
							});
						}
						if (value.cartProductPreOrderResponses) {
							value.cartProductPreOrderResponses.forEach((product) => {
								console.log(product);
								if (product.quantity && product.price) {
									this.totalProductQuantity = this.totalProductQuantity + product.quantity;
									this.totalProductPrice = this.totalProductPrice + product.quantity * product.price;
								} else {
								}
							});
						}
					});
				} else {
					this.totalProductQuantity = 0; //calculate the quantity again
					this.totalProductPrice = 0; //calculate the price again
				}
			},
			(error) => {
				this.appErrorHandler.handleErrorWithNoti(error);
			}
		);
	}
	changeQuantity(changeQuantity: number, productId: number, productType: number) {
		this.cartService
			.changeQuantity(productId, changeQuantity, productType)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result.isSuccessed) {
						if (result.message) {
							this.noti.showSuccessNotification(result.message);
						}
						this.cartShopResponses.forEach((value) => {
							if (value.cartProductResponses) {
								value.cartProductResponses.forEach((product) => {
									if (product.productId === productId) {
										product.quantity = changeQuantity;
									}
								});
							} 
							if (value.cartGroupProductResponses) {
								value.cartGroupProductResponses.forEach((product) => {
									if (product.groupProductId === productId) {
										product.quantity = changeQuantity;
									}
								});
							} 
							if (value.cartProductPreOrderResponses) {
								value.cartProductPreOrderResponses.forEach((product) => {
									if (product.productId === productId) {
										product.quantity = changeQuantity;
									}
								});
							} else {
							}
						});
						this.cartShopResponses$.next(this.cartShopResponses);
					} else {
						if (result.message) {
							this.noti.showErrorNotification(result.message);
						}
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				},
				() => {}
			);
	}
	deleteCardProduct(productId: number, productType: number) {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		this.cartService
			.deleteCardProduct(productId, productType)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result.isSuccessed) {
						if (result.message) {
							this.noti.showSuccessNotification(result.message);
						}
						this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
						this.getCart();
					} else {
						if (result.message) {
							this.noti.showErrorNotification(result.message);
						}
					}
					this.cartShopResponses$.next(this.cartShopResponses);
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				},
				() => {}
			);
	}
	order() {
		if (this.cartShopResponses.length > 0) {
			this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
			this.cartService
				.order()
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(result) => {
						if (result.isSuccessed) {
							if (result.message) {
								this.noti.showSuccessNotification(result.message);
							}
							this.getCart();
							this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
						} else {
							if (result.message) {
								this.noti.showErrorNotification(result.message);
							}
						}
					},
					(error) => {
						this.appErrorHandler.handleErrorWithNoti(error);
					},
					() => {}
				);
		} else {
			this.noti.showSuccessNotification('Không Có Sản Phẩm');
		}
	}
	deleteCart() {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		this.cartService.deleteCart().subscribe(
			(result) => {
				if (result) {
					if (result.isSuccessed) {
						this.cartShopResponses = [];
						this.cartShopResponses$.next(this.cartShopResponses);

						if (result.message) {
							this.noti.showSuccessNotification(result.message);
						}
					} else {
						if (result.message) {
							this.noti.showErrorNotification(result.message);
						}
					}
				} else {
				}
				this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
			},
			(error) => {
				this.appErrorHandler.handleErrorWithNoti(error);
				this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
			}
		);
	}
	checkLogin() {
		if (this.authService.isAuthenticated('customer-token')) {
			this.isLogin = true;
			//this.getProductSimilar();
		}
	}
	similarProduct(productId) {
		this.getProductSimilar();
		this.getProductCosin(productId);
	}
	getProductSimilar() {
		this.isProductSuggestionProductSimilarLoading = true;
		this.suggestService
			.getProductsBySimilarProduct()
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						this.apiResultProductShowHomePageResponds = result;
						console.log(this.apiResultProductShowHomePageResponds);
						this.isProductSuggestionProductSimilarLoading = false;
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	getProductCosin(productId: number) {
		this.isProductSuggestionProductCosinLoading = true;
		this.suggestService
			.getProductsByCosin(productId)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						this.apiResultProductSuggestCosin = result;
						console.log(this.apiResultProductSuggestCosin);
						this.isProductSuggestionProductCosinLoading = false;
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
}
