import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Title} from '@angular/platform-browser';
import {ActivatedRoute, Router} from '@angular/router';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {CustomerLogin} from 'src/app/core/models/customer/login/request/customer-login.viewmodel';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {CustomerAppState} from 'src/app/core/store/customer/customer-app.state';
import {select, Store} from '@ngrx/store';
import {customerLoginAction} from 'src/app/core/store/customer/login/customer-login.actions';
import {takeUntil} from 'rxjs/operators';
import {customerLoginStateSelector} from 'src/app/core/store/customer/login/customer-login.selector';
import { SpinnerService } from 'src/app/core/services/comon-services/spinner/spinner.service';

@Component({
	selector: 'app-customer-login',
	templateUrl: './customer-login.component.html',
	styleUrls: ['./customer-login.component.less']
})
export class CustomerLoginComponent implements OnInit {
	submitted = false; //if submited => spinner display until return result
	returnUrl: string;
	customerLoginForm!: FormGroup;
	constructor(
		private route: ActivatedRoute,
		private formBuilder: FormBuilder,
		private titleService: Title,
		private appErrorHandler: AppErrorHandler,
		private store: Store<CustomerAppState>,
		private destroy$: DestroyService,
		private router: Router,
		private spinner: SpinnerService
	) {
		this.returnUrl = ''; //init value
	}

	ngOnInit(): void {
		try {
			this.titleService.setTitle('login'); //set title
			this.checkLogin();
			this.initForm();
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
		this.returnUrl = this.route.snapshot.queryParams.returnUrl || '';
	}

	checkLogin() {
		this.store.pipe(takeUntil(this.destroy$), select(customerLoginStateSelector)).subscribe(
			(result) => {
				if (result.isAuthenticated == true && result.status == 'loaded') { //if logged and loaded successfully
					this.router.navigate([this.returnUrl]); //navigate returnUrl
				}
				if (result.status == 'loading') { //loading => do nothing
				} else { //loaded and authen is false
					this.submitted = false; // turn off spinner
				}
			},
			(error) => { //error => turn off spinner
				this.submitted = false;
				this.appErrorHandler.handleErrorWithNoti(error);
			},
		);
	}
	initForm() {
		try {
			this.customerLoginForm = this.formBuilder.group({
				email: ['', [Validators.required, Validators.minLength(3), Validators.email]],
				password: ['', [Validators.required, Validators.minLength(8), Validators.maxLength(20)]],
				remember: [true]
			});
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}
	submitForm(): void { //run when click submit
		try {
			if (this.customerLoginForm.invalid) { //if input is valid
				return; //do nothing
			} else {
				
				this.submitted = true; //tunr on spinner
				const val = this.customerLoginForm.value; //create object login
				const customerLogin: CustomerLogin = {
					email: val.email,
					passWord: val.password,
					remember: val.remember,
					urlReturn: ''
				};
				this.store.dispatch(customerLoginAction({customerLogin})); //call action login => status: loading, is Authenticated: false, message:''
			}
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}
}

