import {Injectable} from '@angular/core';
import {CustomerUrlBase} from './../customer-url-base';
@Injectable({
	providedIn: 'root'
})
export class CustomerUrlCart extends CustomerUrlBase {
	customerGetCart(): string {
		return this.customerUrl + '/cart/cart';
	}
	customerPostAddToCart(): string {
		return this.customerUrl + `/cart/cart`;
	}
	customerPostChangeQuantity(): string {
		return this.customerUrl + `/cart/cart-change-count`;
	}
	customerPostBuyNow() {
		return this.customerUrl + `/cart/buy-now`;
	}
	customerDeleteCardProduct(productId: number, productType: number) {
		return this.customerUrl + `/cart/card-product/${productId}?productType=` + productType;
	}
	customerOrder() {
		return this.customerUrl + `/cart/order`;
	}
	getCartProductCount(): string {
		return this.customerUrl + `/cart/cart-count`;
	}
	deleteCart(): string {
		return this.customerUrl + `/cart/delete`;
	}
}
