import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {Component, Input, OnInit} from '@angular/core';
import {ProductShowHomePageRespond} from 'src/app/core/models/product/respond/ProductShowHomePageRespond';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {environment} from 'src/environments/environment';

@Component({
	selector: 'app-product-suggest',
	templateUrl: './product-suggest.component.html',
	styleUrls: ['./product-suggest.component.less']
})
export class ProductSuggestComponent implements OnInit {
	@Input() productSuggestTitle: string = '';
	@Input() apiResult?: ApiResult<Array<ProductShowHomePageRespond>> = {};
	productShowHomePageResponds?: Array<ProductShowHomePageRespond> = [];
	host: string = environment.apiUrl;
	constructor(private noti: NotificationService) {}
	ngOnInit(): void {
		if (this.apiResult) {
			console.log(this.apiResult);
			if (this.apiResult.isSuccessed) {
				console.log(this.apiResult);
				if (this.apiResult.resultObj) {
					this.productShowHomePageResponds = this.apiResult.resultObj;
				} else {
					if (this.apiResult.message) {
						this.noti.showErrorNotification(this.apiResult.message);
					}
				}
			} else {
				if (this.apiResult.message) {
					this.noti.showErrorNotification(this.apiResult.message);
				}
			}
		}
	}
}
